@tailwind base;
@layer base {
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  html {
    @apply text-neutral-800 w-full overflow-x-hidden;
  }
  html.dark {
    @apply text-neutral-50;
    @apply bg-neutral-800;
  }
}
@tailwind components;
@tailwind utilities;

.body {
  @apply w-full m-0 p-0;
}
.show {
  @apply transition-all duration-500 ease-in overflow-y-visible max-h-[45rem];
}
.hide {
  @apply transition-all opacity-0 duration-500 ease-out overflow-y-hidden max-h-0;
}
.boxes {
  @apply abs py-9 px-6 w-[17rem] cursor-pointer border-[1px] border-green-300 
  text-sm text-center flex flex-col items-center bg-white rounded-xl transition 
  duration-200 hover:shadow-none;
}
.BottomBox {
  @apply abs py-9 px-6 w-full cursor-pointer border-[1px] border-green-300 
  text-sm text-center flex flex-col items-center bg-white rounded-xl transition 
  duration-200 hover:shadow-none;
}
.Docboxes {
  @apply abs py-4 px-6 w-[30rem] cursor-pointer border-[1px] text-sm 
  text-left flex flex-col items-start bg-white rounded-xl transition duration-200 
  hover:shadow-none;
}
.Adminboxes {
  @apply abs py-4 px-6 w-[17rem] cursor-pointer border-[1px] text-sm 
  text-left flex flex-col items-start bg-white rounded-xl transition duration-200 
  hover:shadow-none;
}
.AdminboxesBot {
  @apply abs py-4 px-6 w-full cursor-pointer border-[1px] text-sm 
  text-left flex flex-col items-start bg-white rounded-xl transition duration-200 
  hover:shadow-none;
}
.imgDash {
  @apply mb-7 w-[13rem] h-[13rem] bg-[#a7eb9c85] rounded-2xl;
}
.imgDashBottom {
  @apply mb-7 w-full h-[13rem] bg-[#a7eb9c85] rounded-2xl;
}
.titleText {
  @apply text-white hover:bg-opacity-80 transition duration-100 font-semibold text-lg mb-3 text-center w-full bg-green-600;
}
.AdmintitleText {
  @apply font-semibold text-lg mb-3;
}

.nav:hover {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.658);
  color: white;
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-thickness: 2px;
  transition: 150ms;
  margin-top: -2px;
}

