.swirl-in-fwd {
  animation: swirl-in-fwd 0.6s ease-out both;
}
.abs {
  box-shadow: 1px 2px 15px rgba(0, 0, 0, 0.35);
}
.drop {
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
}

.roll-in-left {
  -webkit-animation: roll-in-left 1s ease-out 0.2s both;
  animation: roll-in-left 1s ease-out 0.2s both;
}

.find {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
}

.docs {
  box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.08);
}
.docs:hover {
  box-shadow: none;
}
.searchbg {
  background-color: rgba(255, 255, 255, 0.87);
  box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.3);
}

@media (max-width: 320px) {
  .carousel {
    @apply h-[10rem];
  }
}
@media (max-width: 1654px) {
  .carousel {
    @apply h-[32rem];
  }
}
@media (max-width: 1366px) {
  .docs {
    @apply w-[12rem] text-[13px];
  }
  .valueDoc {
    @apply text-[12px] max-w-[5rem] whitespace-nowrap;
  }
  .Docbtn {
    @apply text-[12px] whitespace-nowrap py-0;
  }
  .Doctors {
    @apply w-[60rem];
  }
}
@media (max-width: 1654px) {
  .carousel {
    @apply h-[32rem];
  }
}
@media (max-width: 765px) {
  .carouselmain {
    @apply w-[80%];
  }
  .dots {
    @apply mb-20;
  }
}
@media (max-width: 960px) {
  .search_box {
    @apply w-full;
  }
  .search_label {
    @apply text-base;
  }
  .serachInput {
    @apply w-[10rem] text-base;
  }
}
@media (max-width: 818px) {
  .search_box {
    @apply w-full py-6;
  }
  .search_label {
    @apply text-base;
  }
  .serachInput {
    @apply text-xs;
  }
  .searchbutton {
    @apply text-base w-[30%];
  }
  .resetbutton {
    @apply text-sm;
  }
}
@media (max-width: 595px) {
  .search_box {
    @apply w-full py-4;
  }
  .search_label {
    @apply text-sm;
  }
  .serachInput {
    @apply w-[5rem] text-sm;
  }
  .searchbutton {
    @apply text-base w-[30%];
  }
  .resetbutton {
    @apply text-sm;
  }
}
@media (max-width: 425px) {
  .features {
    @apply grid-cols-2 gap-x-4 gap-y-7;
  }
  .boxes {
    @apply w-[10rem] py-6 text-[12px] h-[18rem];
  }
  .titleText {
    @apply text-white hover:bg-opacity-80 transition duration-100 
    font-semibold text-sm mb-3 text-center w-full bg-green-600;
  }
  .imgDash {
    @apply mb-7 w-[7rem] h-[7rem] bg-[#a7eb9c85] rounded-2xl;
  }
  .search_label {
    @apply text-sm;
  }
  .containtsearch {
    @apply grid-cols-2 gap-y-5 ml-5;
  }
  .search_label {
    @apply text-sm;
  }
  .serachInput {
    @apply w-[80%];
  }
  .outbox {
    @apply px-3;
  }
  .searchbutton {
    @apply text-sm;
  }
  .resetbutton {
    @apply text-xs;
  }
  .fillup {
    @apply grid grid-cols-1;
  }
}
